<template>
    <footer class="footer">
        <textarea 
        rows="1"
        class="footer__textarea"
        placeholder="Написать сообщение..."
        v-model="message"
        @input="areaText"
        />
        <button class="footer__photo" v-if="!message" @click="clouseModal = true">
            <img src="@/assets/img/photo.svg" alt="">
        </button>
        <button class="footer__photo" v-else @click="sendMess">
            <img src="@/assets/img/send.svg" alt="">
        </button>
    </footer>
    
    
    <Transition name="bounce">
    <div class="modal" @click="clouseModal = false" v-if="clouseModal">
        <div class="modal__block" @click.stop="">
            <h2 class="modal__block-title">Отправить картинку</h2>
            <div class="modal__block-inputs">
                <label for="inp">
                    <span>URL</span>
                    <input type="text" id="inp" v-model="url">
                </label>
                <label for="area">
                    <span>Comment</span>
                    <textarea type="area" id="inp" v-model="comment"></textarea>
                </label>
            </div>
            
            <div class="modal__block-btns">
                <button class="modal__block-btns-btn dell" @click="clouseModal = false">Отмена</button>
                <button class="modal__block-btns-btn edit" @click="sendPhoto">Отправить</button>
            </div>
        </div>
    </div>
</Transition>
</template>

<script>
    export default {
        data() {
            return {
                message: '',
                clouseModal: false,
                url: '',
                comment: '',
            }
        },
        props:['id'],
        methods:{
            areaText(){
                const area = document.querySelectorAll('.footer__textarea')
                area.forEach(el => {
                    el.style.height = `auto`
                    el.style.height = `${el.scrollHeight}px`
                })
            },
            sendMess(){
                const min = new Date().getMinutes()
                let times = min >= 10 ? min : `0${min}`
                
                if (this.message.trim().length) {
                    const mess = {
                        id: this.id,
                        sms: this.message,
                        time: `${new Date().getHours()}:${times}`
                    }
                    this.$emit('sendMess', mess)
                    this.message = ''
                    
                    const area = document.querySelectorAll('.footer__textarea')
                    area.forEach(el => {
                    el.style.height = `auto`
                })
                }
            },
            sendPhoto(){
                const min = new Date().getMinutes()
                let times = min >= 10 ? min : `0${min}`
                
                if (this.url.trim().length && this.comment.trim().length) {
                    const mess = {
                        id: this.id,
                        sms: this.comment,
                        url: this.url,
                        time: `${new Date().getHours()}:${times}`
                    }
                    this.$emit('sendMess', mess)
                    this.url = this.comment = ''
                    this.clouseModal = false
                }
            }
        }
    }
    
</script>

<style lang="scss" scoped>

</style>