<template>
    <main class="main">
       <div class="main__block"
        v-for="(sms, index) in smsStorage" 
        :key="index"
        :class="{
            edit: sms.id == id,
            send: sms.id !== id
         }">
        <div class="main__block-item">
            <div class="main__block-item-content">
                <img :src="sms.url" alt="" v-if="sms.url">
                <p>{{ sms.sms }}</p>
            </div>
            <span>{{ sms.time }}</span>
        </div>
       </div>
    </main>
</template>

<script>
    export default {
        props:['smsStorage', 'id']
    }
</script>

